body {
	width: 100%;
	margin: auto;
	padding: 0;
	background: #ededed;
	font-family: sans-serif;
	font-size: 14px;
}

a {
	font-weight: bold;
	text-decoration: none;
	color: #1559BB;
	transition: 0.2s ease all;
}

a:hover {
	color: #FFCE95;
}

.container {
	background: #fff;
	width: 100%;
	max-width: 960px;
	margin: auto;
}

header {
	background: #1559BB;
	padding: 1em;
	display: grid;

}

.navLogo {
	max-width: 40%;
}

.search {
	margin: 4em 0 0 20em;
}




.search select {

	margin-left: 2%;
	margin-right: 2%;
}


header button#menuToggle {
	z-index: 1000;
	position: absolute;
	top: 6px;
	left: 0px;
	background: transparent;
	outline: 0;
	border: 0;
	display: none;
}

header button#menuToggle svg {
	fill: #fff;
	width: 40px;
	height: 40px;
}

footer {
	background-color: #FFCE95;
	border-top: 12px solid #FFCE95;
	border-radius: 2px;
	text-align: center;
	height: auto;
}

footer .logo img {
	max-width: 40%;
	height: auto;
	margin: 0 auto;
}



.nav {
	padding: .3em;
	margin: 1em 16em;
	width: 100%;
}

.nav a {
	text-align: center;
	color: #fff;
	font-weight: normal;
	margin: 0 .5em;
}

.nav a:hover {
	color: #FFCE95;
}


.spinner {
	position: absolute;
	display: flex;
	justify-content: center;
	height: 100vh;
	width: 100vw;
	background-color: white;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}

/* NavBar Links */

.main {
	display: grid;
	grid-template-columns: 50% 50%;
	margin-top: 3em;
}

.left {
	grid-column: 1;
	padding: 0 30px;
}

.profile {
	display: grid;
	grid-gap: 1em;
	align-items: center;
}

.profile .profilePicture {
	grid-column: 1/2;
	text-align: center;
	margin: auto;
}

.profile .profileInfo {
	grid-column: 2/4;
}

.profile .profileInfo p {
	line-height: 1em;
	font-size: 13px;
}

.onlineNow {
	position: relative;
	margin-left: 20px;
	padding: 15px 0;
	color: green;
	font-weight: bold;
	font-size: 12px;
}

.onlineNow:before {
	content: '';
	position: absolute;
	top: 7px;
	left: -25px;
	display: block;
	height: 20px;
	width: 20px;
	background: url('../images/online.gif');
	background-size: cover;
}

.tagline {
	display: block;
	margin: 10px 0 25px 0;
}

.contact {
	border: 1px solid #ccc;
}

.contact h3,
.userDetails h3 {
	background-color: #639BD0;
	color: #fff;
	font-size: 13px;
	font-weight: bold;
	padding: 5px 10px;
	margin: 0;
	border-bottom: 1px solid #ccc;
}

/* details table */
#root>div>div>div>span>div>div.left>div.userDetails {
	width: 66%;
}

.contactNav ul {
	margin: 0;
	list-style: none;
	display: inline-block;
}

.contactNav ul li {
	position: relative;
	padding: 5px 8px;

}

.contactNav ul li a {
	font-size: 12px;
	transition: 0.2s ease all;
	color: #1559BB;
}

.contactNav ul li svg {
	position: absolute;
	top: 6px;
	left: -20px;
	display: block;
	height: 15px;
	width: 15px;
	background-size: cover;
	fill: rgb(204, 204, 204);
	transition: 0.2s ease all;
}

.contactNav ul li:hover a,
.contactNav ul li:hover svg {
	color: #FFCE95;
	fill: #FFCE95;
}

.userUrl {
	padding: 1em;
	margin: 2em 0;
	border: 1px solid #ccc;
	color: #1559BB;

}

.userUrl h4 a {
	margin: 0;

}



.userDetails {
	border: 1px solid #ccc;
	text-align: left;
	margin-bottom: 30px;
}

.userDetails th {
	padding: 2px;
	font-size: 12px;
}

.userDetails th:first-child {
	background: #B0D0F2;
	color: #527DAB;
	width: 120px;
}

.userDetails th:last-child {
	background: #D6EBF7;
	width: 226px;
	font-weight: normal;
}

.right {
	grid-column: 2;
	/* width: 50%;
	float: right; */
}

.right .network {
	text-align: center;
	padding: 20px 10px;
	border: 1px solid #000;
	font-size: 18px;
	font-weight: bold;
}

.right h3 {
	display: block;
	background: #FFCE95;
	color: #FD741B;
	padding: 2px 10px;
	font-size: 14px;
}

.right .blurb h4 {
	color: #FD741B;
	margin: 0;
	padding: 0;
}

.right .blurb p {
	padding: 0;
	margin: 0 0 20px 0;
}

.top8 {
	display: grid;
	grid-template-columns: auto auto auto auto;
	grid-gap: 10px;
	margin-bottom: 30px;
	justify-content: space-around;
}

.top8 div {
	text-align: center;
	line-height: 1.8em;
}

.top8 div img {
	display: block;
	height: 80px;
	width: 80px;
	margin: auto;
	border-radius: 5px;
	border: 3px solid transparent;
}

.top8 div a:hover img {
	border-color: #FFCE95;
}

@media screen and (max-width: 940px) {
	.musicPlayer .songName {
		width: 75%;
	}

	.musicPlayer .songName #playing {
		right: 5px;
		width: 120px;
	}
}

@media screen and (max-width: 732px) {
	#menuToggle {
		display: block !important;
	}

	.showNav {
		height: 100% !important;
	}

	.open svg {
		transform: rotate(135deg);
	}

	#menuToggle svg:hover {
		cursor: pointer;
	}

	#menuToggle svg {
		transition: 0.2s ease all;
	}

	#menuToggle svg.close {
		display: none;
	}

	#menuToggle.open svg.opened {
		display: none !important;
	}

	#menuToggle.open svg.close {
		display: block !important;
	}

	.nav {
		flex-direction: column;
		text-align: center;
		height: 0;
		overflow: hidden;
	}

	.nav div a {
		width: 100%;
		padding: 10px 0;
		display: block;
	}

	.nav div:nth-child(8) {
		display: none;
	}

	.profile {
		justify-content: center;
	}

	.profilePic {
		display: block;
		width: 100px;
	}

	.lastLogin {
		margin-bottom: 30px;
	}

	.profile .profilePicture {
		grid-column: 1/4;
		grid-row: 1;
		text-align: center;
		margin: auto;
	}

	.profile .profileInfo {
		grid-column: 1/4;
		grid-row: 2;
	}

	.userUrl {
		font-size: 10px;
	}

	.musicPlayer .songName {
		width: 68%;
	}

	span.title,
	span.artist {
		font-size: 10px !important;
	}

	.songName {
		line-height: 12px;
	}

	.top8 {
		grid-template-columns: auto auto auto;
	}
}

@media screen and (max-width: 630px) {
	.left {
		grid-column: 1/4;
		grid-row: 1;
		max-width: 400px;
		margin: 0px auto 20px auto;
	}

	.profile .profilePicture {
		grid-column: 1/2;
		grid-row: 1;
		text-align: center;
		margin: auto;
	}

	.profile .profileInfo {
		grid-column: 3/4;
		grid-row: 1;
	}

	.userDetails {
		margin: 0;
	}

	.musicPlayer {
		max-width: 250px;
		margin: 20px auto 20px auto;
	}

	.right {
		grid-column: 1/4;
		grid-row: 2;
		max-width: 360px;
		margin: auto;
	}

	.right .network {
		font-size: 14px;
		padding: 0;
	}
}